.nav-filter {
    color: #777;
    font-family: "Inter", sans-serif;
    font-size: 14px;
    padding: 8px 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    /* @include respond(tablet) {
        grid-template-columns: 1fr 1fr;
    } */

    &-item-dropdown {
        width: 100%;
        padding: 0 16px;
        line-height: 48px;
        text-decoration: none;
        color: #777;

        .arrow.up {
            vertical-align: bottom;
        }

        .arrow.down {
            vertical-align: top;
        }
    }

    &-item-dropdown.active {
    }

    &-item-dropdown.active i:first-child {
        display: none;
    }

    &-item-dropdown.active i:last-child {
        display: inline-block;
    }

    &-item-dropdown i:first-child {
        display: inline-block;
    }

    &-item-dropdown i:last-child {
        display: none;
    }

    &-item {
        position: relative;
        display: flex;
        min-width: 120px;
        height: 48px;
        margin-right: 12px;
        border: 1px solid #777;
        text-align: center;
    }

    &-dropdown-content {
        display: none;
        position: absolute;
        top: calc(100% + 1px);
        left: -1px;
        background-color: #ffffff;
        //width: 100%;
        min-width: 160px;
        text-align: left;
        z-index: 1;
        border: 1px solid #c9c9c9;

        &-active {
            font-weight: 700;
        }

        & a {
            color: black;
            padding: 12px 16px;
            text-decoration: none;
            display: block;
            white-space: nowrap;
        }

        & a:hover {
        }

        &.active {
            display: block;
        }

        & ul {
            list-style-type: none;
            margin: 0;
            padding: 0;
        }

        & > ul > li {
            position: relative;
            display: flex;
            padding: 15px;

            & + li {
                &::before {
                    position: absolute;
                    content: '';
                    top: 0;
                    left: 15px;
                    width: calc(100% - 30px);
                    height: 1px;
                    background-color: #e7e7e7;
                }
            }

            & label {
                white-space: nowrap;
            }
        }

        & > ul > ul > li {
            padding-left: 25px !important;
            padding: 15px;
        }

        & ul li:hover {
            background-color: #f4eddd;
        }
    }
}

@media only screen and (max-width: 980px) {
    .nav-filter {
        grid-template-columns: 1fr 1fr;
    }
}
