.align-right {
    margin-left: auto;
}

.align-left {
    margin-right: auto;
}

.scroll-lock {
    overflow: hidden;
}

.mdl-popup {
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    overflow-y: auto;
    background-color: rgba(0,0,0,0.25);
    z-index: 201;
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s ease-in-out;

    &.visible {
        visibility: visible;
        opacity: 1;
    }
}

.popup-container {
    position: relative;
    width: calc(100% - 2rem);
    max-width: 480px;
    margin: auto;
    padding-top: 2rem;
    padding-right: 3rem;
    padding-bottom: 1rem;
    padding-left: 3rem;
    background-color: white;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: 6px;
    box-shadow: 0 5px 15px rgb(0 0 0 / 50%);
    outline: 0;

    .btn-close {
        position: absolute;
        top: 1rem;
        right: 1rem;
        width: 2rem;
        height: 2rem;
        cursor: pointer;

        &::before,
        &::after {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 2px;
            height: 20px;
            background-color: #333;
            transform: rotate(45deg) translate(-50%, -50%);
            transform-origin: top left;
            content: '';
        }

        &::after {
            transform: rotate(-45deg) translate(-50%, -50%);
        }
    }
}

.popup-content {
    max-height: 0;
    overflow: hidden;
    visibility: hidden;
    opacity: 0;
    transition: max-height .3s ease-in-out, opacity .75s ease-in-out;

    &.visible {
        visibility: visible;
        opacity: 1;
        max-height: 100vh;
    }

    .btn {
        border: 1px solid transparent;
        border-radius: 3px;
        display: inline-block;
        font-style: normal;
        font-weight: 400;
        padding: 10px 18px;
        cursor: pointer;
        text-align: center;
        transition: border-color .1s, box-shadow .1s;
        text-decoration: none;
        line-height: 1.5;

        &-block {
            width: 100%;
        }

        &-primary {
            background-color: #47bb49;
            color: #fff;
        }

        &-link {
            line-height: 1.42857143;
        }

        &-facebook {
            background-color: #4b70b0;
            box-shadow: inset 0 1px 0 rgb(252 252 252 / 20%), 0 1px 1px rgb(0 0 0 / 10%);
            border-color: #3e4d6d #2a3959 #2a3959;
            color: #fff;
            background-image: linear-gradient(to bottom left,#4b70b0,#3c5b9a);
        }
    }
}

.popup-header {
    border-bottom: 1px solid #e5e5e5;

    .form-icon {
        display: block;
        margin: 20px auto 10px auto;
    }
}

.popup-title {
    text-align: left;
    line-height: 1.42857143;
}

.input-title {
    font-size: 0.875rem;
    color: #969696;
}

.address {
    margin-top: 2rem;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;

    .frm-group {
        width: 47%;
        margin-top: 0;
    }
}

.popup-body {
    padding: 1rem 0;
}

.popup-footer {
    text-align: center;
}

.popup-divider {
    position: relative;
    outline: 0;
    border: 0;
    text-align: center;
    opacity: .5;
    margin: 12px 0;
    height: 15px;

    &::before {
        content: "";
        background: linear-gradient(90deg,transparent,#818078,transparent);
        position: absolute;
        left: 0;
        top: 50%;
        width: 100%;
        height: 1px;
    }

    &::after {
        content: attr(data-content);
        position: relative;
        display: inline-block;
        padding: 0 12px;
        background: #fff;
    }
}